// 视频列表组件
<template>
  <div class="video_list">
    <!-- 轮播图 -->
    <swiper :options="swiperOption" 
            ref="mySwiper" 
            @slideChangeTransitionStart="changeTabsIndex()">
        <swiper-slide class="swiper-item" v-for="(slide, index) in swiperSlides" :key="index">
            <!-- loading加载效果组件 -->
            <vue-loading 
                    ref="loads" 
                    class="loading" 
                    type="bars" 
                    color="#333333" 
                    :size="{ width: '1rem', height: '1rem' }"></vue-loading>
            <!-- 视频 @click='controlVideo(index)' -->
            <video @click='controlVideo(index)'
                   ref="video" autobuffer 
                   @canplay="loadEnd(index)" 
                   :src="slide.url"></video>
            <!-- 自定义播放按钮 -->
            <div class="play-bar" ref="playbar" @click='controlVideo(index)'>
                <img src="../assets/image/playbar.png" alt="播放按钮">
            </div>
        </swiper-slide>
    </swiper>
    <div class="summary">{{this.swiperSlides[this.tabsIndex]['title']}}</div>
    <div class="page-num">
        <span>{{this.tabsIndex + 1}}</span>
        <span>{{this.swiperSlides.length}}</span>
    </div>
  </div>
</template>

<script>
import { swiper, swiperSlide } from "vue-awesome-swiper";
import "swiper/dist/css/swiper.css";
import { VueLoading } from "vue-loading-template";
export default {
  name: "video_list",
  data() {
    return {
      tabsIndex: 0, // 当前显示第几页
      // 轮播图参数配置
      swiperOption: {
        // loop: true
      },
      // 轮播渲染数组(默认示例值)
      swiperSlides: [
        {
          title: "视频demo",
          url: "https://bd.bendichihe.com/data/video/video_1.mp4"
          // http://babylife.qiniudn.com/FtRVyPQHHocjVYjeJSrcwDkApTLQ
        }
      ]
    };
  },
  props: {
    initData: {}
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper;
    }
  },
  mounted() {},
  watch: {
    // 监听父组件传递给initData的值变化
    initData(value) {
      if (value && value.length > 0) {
        this.swiperSlides = value;
      }
    }
  },
  methods: {
    // 轮播图切换事件
    changeTabsIndex() {
      // 更新当前显示第几页的数字
      this.tabsIndex = this.swiper.activeIndex;
      //
      this.$refs["video"][this.swiper.activeIndex].pause();
      this.$refs["playbar"][this.swiper.previousIndex].style.display = "block";
    },
    // 控制播放与暂停视频
    controlVideo(index) {
      // 获取当前video视频元素来操作
      // var el = event.currentTarget;
      var el = this.$refs["video"][index];
      // 播放与暂停切换
      if (el.paused) {
        el.play();
        this.$refs["playbar"][index].style.display = "none";
      } else {
        el.pause();
        this.$refs["playbar"][index].style.display = "block";
      }
    },
    // 视频加载完毕可播放时触发
    loadEnd(index) {
      // 对应视频的加载动画消失
      this.$refs["loads"][index].$el.style.display = "none";
    }
  },
  components: {
    swiper,
    swiperSlide,
    VueLoading
  }
};
</script>
<style scoped lang="less">
.video_list {
  height: 6rem;
  overflow: hidden;
  position: relative;
  border: 0.01rem solid #ccc;
}
video {
  width: 100%;
  height: 6rem;
  // object-fit: cover;
  background: #333333;
}
.summary {
  position: absolute;
  z-index: 99;
  left: 0.3rem;
  bottom: 0.5rem;
  font-size: 0.3rem;
  color: white;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  width: 82%;
}
.page-num {
  position: absolute;
  z-index: 99;
  right: 0.2rem;
  bottom: 0.2rem;
  font-size: 0.3rem;
  width: 0.7rem;
  height: 0.7rem;
  border-radius: 50%;
  background-color: #525252;
  color: white;
  span:first-of-type {
    position: absolute;
    left: 20%;
    top: 17%;
    font-size: 0.35rem;
  }
  span:last-of-type {
    position: absolute;
    right: 20%;
    bottom: 24%;
    font-size: 0.1rem;
  }
  &:after {
    content: "|";
    display: block;
    position: absolute;
    left: 50%;
    top: 22%;
    transform: rotate(20deg);
  }
}
.loading {
  position: absolute;
  z-index: 99;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
}
.play-bar {
  width: 0.98rem;
  position: absolute;
  z-index: 99;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.swiper-container {
  height: 100%;
}
</style>
