// 文章列表组件

<template>
  <div class="content_list" @click="gotoDetail()">
      <div class="content">
          <img v-if="!this.initData.type" class="con_img" v-lazy="this.initData.url" :alt="this.initData.describe">
          <!--  controls="controls" -->
          <video v-else :src="this.initData.url" ref="video" @click='controlVideo'>
            您的浏览器不支持视频播放
          </video>
          <!-- 自定义播放按钮 -->
          <div v-show="playBarBol" class="play-bar" ref="playbar" @click='controlVideo'>
              <img src="../assets/image/playbar.png" alt="播放按钮">
          </div>
      </div>
      <div class="message">
          <h2>{{this.initData.title ? this.initData.title : "文章标题"}}</h2>
          <p>{{this.initData.content ? this.initData.content : "内容简介"}}</p>
          <span>{{this.initData.time}}</span>
      </div>
  </div>
</template>

<script>
export default {
  name: "content_list",
  data() {
    return {
      // 控制自定播放按钮显示与隐藏
      playBarBol: false
    };
  },
  props: {
    // 期望数据格式： {type: '0:图片 / 1:视频', url: 'http://xxx', describe:'描述', id:1, title:"文章标题", content:'内容简介'，time:'2018.09.10'}
    initData: {}
  },
  mounted() {
    this.playBarBol = this.initData.type;
  },
  methods: {
    controlVideo() {
      // 获取当前video视频元素来操作
      var el = this.$refs.video;
      // 播放与暂停切换
      if (el.paused) {
        el.play();
        // 全屏播放
        el.webkitRequestFullScreen();
      } else {
        el.pause();
      }
      this.playBarBol = !this.playBarBol;
    },

    // 跳转到详情页面，并传递id过去
    gotoDetail() {
      // 判断图片还是视频（目前只有图片列表才有跳转）
      if (this.initData.type == 0) {
        let url = `/contentDetail/${this.initData.id}`; //普通详情页面
        // 判断跳转到普通详情页面(默认) 还是 活动详情页面
        if (this.initData.classify == 2) {
          url = `/activityDetail/${this.initData.id}`; //活动详情页面
        }
        this.$router.push({
          path: url
        });
      }
    }
  },
  components: {}
};
</script>

<style scoped lang="less">
img {
  width: 100%;
}
.content_list {
  width: 95%;
  margin: 0 auto;
  box-sizing: border-box;
  padding: 0 0 0.3rem;
  background-color: #fff;
  border-radius: 0.12rem;
  margin-bottom: 0.3rem;
  overflow: hidden;
}
.content {
  height: 5.4rem;
  overflow: hidden;
  text-align: center;
  background: url("../assets/image/videobg.png") 0 0 no-repeat;
  background-size: contain;
  position: relative;
  > img {
    width: auto;
    height: 100%;
    text-align: center;
  }
  video {
    object-fit: cover;
  }
  .play-bar {
    width: 0.98rem;
    position: absolute;
    z-index: 99;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
  }
}
video {
  width: 100%;
  height: 5.4rem;
  // 视频填充父级标签 fill / over
  // object-fit: fill;
}
.message {
  padding: 0 0.2rem;
  font-size: 0.24rem;
  h2 {
    font-size: 0.3rem;
    font-weight: bold;
    margin: 0.3rem 0 0.16rem;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }
  p {
    color: #696969;
    line-height: 0.3rem;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
  }
  span {
    color: #999;
    display: block;
    margin-top: 0.12rem;
  }
}
</style>
